.joinPage{
    background-color: rgb(31, 29, 29);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    /* border: 2px solid white; */
    /* border: 2px sol id white; */
    /* height: 50%; */
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.container > img{
    width: 10vmax;

}
.container > h1{
  color: white;
  font-size: 2.5vmax;
  width: 30vmax;
  border-bottom: 0.2vmax solid white;
  text-align: center;
  padding: 1vmax ;
  font-family: "Roboto";
  box-sizing: border-box ; 
}
#joinInput{
    width: 25vmax;
    padding: 1.5vmax;
    border: none;
    outline: none;
    font-size: 1.2vmax;
    margin: 2vmax;
    box-sizing: border-box ;
}
.joinBtn{
    width: 25vmax;
    padding: 1vmax;
    border: none;
    outline: none;
    font-size: 1.2vmax;
    /* margin: 2vmax; */
    background-color: crimson;
    color: white;
    font-family: "Roboto";
    transition: all 0.3s;
    cursor: pointer;


}
.joinBtn:hover{
    background-color: rgb(180, 27, 58);
}