.chatPage{
    background-color: rgb(31, 29, 29);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.chatContainer{
    background-color: white;
    height: 60%;
    width: 50%;

}
.header{
    background-color: crimson;
    height: 15%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header>h2{
    color: white;
       margin: 2.5vmax;


}
.header img{
   margin: 2.5vmax;
   transition: all 0.3s;
   cursor: pointer;


}
.header img:hover{
    transform: scale(1.2);
}
.chatBox{
    /* border: 0.2vmax solid black ; */
    height: 70%;
    box-sizing: border-box;
    /* overflow-y: auto; */
}
.inputBox{
    /* border: 0.2vmax solid black; */

     height: 15%; 
     box-sizing: border-box;  
     display: flex;


}
#chatInput{
   
  width: 80%;
  border: none;
  padding:2vmax;
  box-sizing: border-box;
  outline: none;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 1.2vmax;
  background-color: white;
  border-top: 0.2vmax solid rgb(52, 50, 50);
}
.sendBtn{
    background-color: crimson;
    color: white;
    border: none;
    width: 20%;
    transition: all 0.3s;
    cursor: pointer;


}
.sendBtn:hover{
    background-color: rgb(156, 12, 41);
    
}
.sendBtn>img{
    width: 3vmax ;
    filter: brightness(100) invert();
    transition: all 0.3s;
}
.sendBtn:hover img{
    transform: translateX(10px);
}